
function main() {

    const hamburger = document.querySelector('.hamburger__container');
    const hamburger_btn = document.querySelector('.hamburger__btn');
    const nav = document.getElementById('nav');
    var navState = false;
    hamburger.addEventListener('click', handleNav);

    function handleNav(e) {
        console.log('action happened');
        if (!navState) {
            nav.classList.add('open');
            hamburger_btn.classList.add('open');
            navState = true;
        } else {
            nav.classList.remove('open');
            hamburger_btn.classList.remove('open');
            navState = false;
        }
    }
}






window.addEventListener('DOMContentLoaded', main);